@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}*{box-sizing:border-box}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:''}table{border-collapse:collapse;border-spacing:0}

:root {
    /*primary*/
    --primary: hsl(170, 67%, 45%);

    /*neutral*/
    --neutral-very-dark: hsl(183, 100%, 15%);
    --neutral-dark-one: hsl(186, 14%, 43%);
    --neutral-dark-two: hsl(184, 14%, 56%);
    --neutral-light-one: hsl(185, 41%, 84%);
    --neutral-light-two: hsl(189, 41%, 97%);
    --neutral-white: hsl(0, 0%, 100%);

    --neutral-primary-shade: hsl(170, 61%, 72%);
    --neutral-reddish: hsl(8, 84%, 39%);

}

body {
    background-color: hsl(185, 41%, 84%);
    background-color: var(--neutral-light-one);
    font-family: 'Space Mono', monospace;
    font-size: 0.75rem;
    font-weight: 700;
    color: hsl(186, 14%, 43%);
    color: var(--neutral-dark-one)
}

.app-container{width:100%;height:100vh}.app{display:grid;grid-template-rows:3rem 1fr;grid-gap:2rem;justify-content:center;width:100%;margin:5em 0 0 0}.header{display:flex;justify-content:center}

.main-section{display:grid;grid-gap:2rem;background-color:var(--neutral-white);padding:2em;border-radius:10px}@media (min-width: 768px){.main-section{grid-template-columns:1fr 1fr}}.main-section__input-part{display:flex;flex-direction:column;grid-gap:1.75rem;gap:1.75rem;padding:1em 0}.input-part__item{display:flex;flex-direction:column;grid-gap:0.5rem;gap:0.5rem}.rate-choice-section{display:grid;grid-template-columns:repeat(auto-fit, minmax(8rem, 1fr));grid-gap:0.5rem}@media (min-width: 395px){.rate-choice-section{grid-template-columns:repeat(auto-fit, minmax(7rem, 1fr))}}@media (min-width: 768px){.rate-choice-section{grid-template-columns:repeat(auto-fit, minmax(5rem, 1fr))}}.rate{display:flex;justify-content:center;align-items:center}.error-message--wrapper{display:flex;justify-content:space-between}.error-message{color:var(--neutral-reddish)}.label-input--container{min-width:50px;max-width:150px}.label-input{width:100%}.main-section__output-part{display:flex;flex-direction:column;grid-gap:1.5rem;gap:1.5rem;background-color:var(--neutral-very-dark);padding:2em;border-radius:10px}@media (min-width: 768px){.main-section__output-part{justify-content:space-between;padding:3em 2em}}.output-part__items{display:flex;flex-direction:column;grid-gap:1rem;gap:1rem}@media (min-width: 768px){.output-part__items{grid-gap:2.5rem;gap:2.5rem}}.output-part__item{display:flex;justify-content:space-between;align-items:center}.label{display:grid;grid-gap:0.25rem}.label-heading{font-size:0.80rem;color:var(--neutral-white)}.amount{font-size:1.6rem;color:var(--primary)}

.input--container{position:relative}.input{-webkit-appearance:none;appearance:none;background-color:var(--neutral-light-two);text-align:end;width:100%;height:100%;padding:0 0.5em;font:inherit;font-size:24px;color:var(--neutral-very-dark);border:2px solid transparent;border-radius:5px;outline:none}.input::-webkit-input-placeholder{color:var(--neutral-dark-two)}.input:-ms-input-placeholder{color:var(--neutral-dark-two)}.input::placeholder{color:var(--neutral-dark-two)}.input:focus{border:2px solid var(--primary)}.error{border:2px solid var(--neutral-reddish)}.error:focus{border:2px solid var(--neutral-reddish)}.input__icon{content:'';position:absolute;top:0.625rem;left:1rem}

.button{background-color:var(--neutral-very-dark);align-items:center;justify-content:center;width:100%;height:2.75rem;font-family:inherit;font-weight:700;font-size:1.4rem;color:var(--neutral-white);border:1px solid transparent;border-radius:5px;transition:0.1s}.button:hover{cursor:pointer;background-color:var(--neutral-primary-shade);color:var(--neutral-very-dark)}.button:active{cursor:pointer;background-color:var(--primary);color:var(--neutral-very-dark)}.button:disabled{cursor:not-allowed;opacity:0.2}.active{background-color:var(--primary);color:var(--neutral-very-dark)}@media (min-width: 768px){.button{width:100%}}

.button-large{background-color:var(--primary);width:100%;color:var(--neutral-very-dark)}@media (min-width: 768px){.button{width:100%}}

